@import url(https://cdn.jsdelivr.net/npm/bulma@0.9.2/css/bulma.min.css);
* {
  box-sizing:border-box;
  margin: 0px;
  padding: 0px;
}
body {
  height: 100%;
  width: 100%;
}
h1, h2, h3, li {
  font-family: 'Bellota Text', cursive;
}
#hero {
  height: 100vh;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction:column;
          flex-direction:column;
  -webkit-justify-content:center;
          justify-content:center;
  -webkit-align-items:center;
          align-items:center;  
  background: linear-gradient(to bottom, #eacda3, #d6ae7b)
}
#heroTitle{
  width:90%;
  height: 40%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.first {
  font-size: 36px;
}
.second {
  font-size:28px;
}
h2 {
  font-size: 32px;
}
p {
  font-size: 20px;
}
h2 {
  margin: 30px 0 30px 0;
}
ul {
  font-size:24px;
  margin-top:50px;
  text-align:center;
}
li {
  margin-bottom:15px;
}
a {
  color:rgb(58, 58, 58);
}
#about, #projects, #interests, #contact {
  min-height: 100vh; 
  display:-webkit-flex; 
  display:flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items:center;
          align-items:center;
  background:rgb(255, 254, 251);
}
#aboutText {
  height:80vh;
  width:90%;
  border-radius:2%;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction:column;
          flex-direction:column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content:space-around;
          justify-content:space-around;
  margin-top: 30px;
}
#projectsArea, #singleProjectArea {
  width: 95%;
}
#projectsArea {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.slide-container {
  margin-bottom:30px;
}
.card-content {
  padding: 0px;
}
#projectDetails {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
#projectText, #projectLinks {
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  width: 80%;
  text-align:center;
}
#projectLinks {
  margin-bottom: 30px;
}
button {
  width: 150px;
}
#contactLinks {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex: 1 1;
          flex: 1 1;
}
#email {
  width: 150px;
}
#linkedIn {
  width: 140px;
}
#github {
  width: 130px;
}
button {
  margin: 30px 0 0 0;
}

@media (min-width: 1025px) {

  .first {
    font-size: 80px;
  }
  .second {
    font-size: 50px;
  }
  h2 {
    font-size: 50px;
  }
  p {
    font-size: 26px;
  }
  #aboutText {
    width: 900px;
  }
  #projectsArea{
    width: 960px;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
  }
  .slideshow {
    width: 450px;
  }
  #singleProjectArea {
    width: 960px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
  }
  #projectCard {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
  }
  #singleSlideshow {
    width: 50%;
    -webkit-order: 1;
            order: 1;
    margin-top: 100px;
    margin-right: 30px;
  }
  #singleText {
    font-size: 20px;
  }
  #contact {
    min-height: 0px;
  }
  #contactLinks {
    width: 700px;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex: 0 1;
            flex: 0 1;
    margin: 50px;

  }
}








